.home_container {
  width: 100%;
  min-height: 200vh;
  overflow: hidden;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  flex-direction: column;
}
.home_new_container {
  max-width: 1000px;
  padding: 4rem 1rem 0rem 1rem;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}
.home_new_presentation {
  width: 100%;
  max-width: 1000px;
  overflow: hidden;
  display: flex;
  align-items: center;
  flex-direction: column;
  padding-top: 4rem;
}

.home_new_title > h1 {
  font-family: "Roboto", Arial, sans-serif;
  font-size: 100px;
  font-weight: 700;
  line-height: 85px;
  background: linear-gradient(
    -45deg,
    rgb(255, 125, 44),
    rgb(255, 181, 43),
    rgb(22, 121, 22),
    rgb(41, 41, 255),
    rgb(85, 25, 129),
    white,
    rgb(234, 157, 234),
    rgb(83, 24, 125),
    rgb(49, 49, 255),
    rgb(240, 240, 0)
  );
  background-size: 400% 400%;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  animation: gradient-animation_first 5s ease forwards;
}
@keyframes gradient-animation_first {
  0% {
    background-position: 0%;
  }
  100% {
    background-position: 100%;
  }
}
.home_new_title {
  max-width: 1000px;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}

.home_video {
  max-width: 800px;
  z-index: 11;
  margin-bottom: 3.3rem;
  position: relative;
  margin-top: 12px;

}
.home_video::after {
  content: "";
  width: 100%;
  height: 1px;
  background-color: white;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
}

.home_new_presentation > p {
  padding: 1rem 0;
  max-width: 600px;
  font-size: 1.3rem;
  line-height: 1.6;
  letter-spacing: -0.1px;
  color: #4a5568;
  font-family: Roboto, Arial, sans-serif;
  text-align: center;
}
.home_new_btns_container {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: row;
}
.home_new_btns_container > a {
  background-color: var(--green);
  color: white;
  /* width: 200px; */
  font-size: 1rem;
  font-weight: 500;
  padding: 0.75rem 2rem;
  border-radius: 10px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  transition: background-color 0.3s;
  text-align: center;
}
.home_new_btns_container > a:hover {
  background-color: var(--greenHover);
}
.home_new_btns_container > a:focus {
  outline: none;
}
.home_new_btns_container > a:first-child {
  margin-right: 15px;
}

.home_websites_interactions {
  width: 100%;
  background-color: #222222;
}
.home_branding {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
}

/* add this styles to the other sections as well */
.home_pachete {
  width: 100%;
  background-color: #f0f0f0;
  display: flex;
  align-items: flex-start;
  justify-content: center;
  padding: 2rem 0;
}

@media screen and (max-width: 970px) {
  .home_new_presentation {
    padding-top: 3rem;
  }
}
@media screen and (max-width: 540px) {
  .home_new_title > h1 {
    font-size: 72px;
    line-height: 62px;
    letter-spacing: -1px;
  }
  .home_new_presentation > p {
    padding: 1rem 0;
    max-width: 600px;
    font-size: 1.125rem;
    line-height: 1.4;
    letter-spacing: -0.1px;
  }
}
@media screen and (max-width: 360px) {
  .home_new_title > h1 {
    font-size: 62px;
    line-height: 52px;
  }
}
